"use client";

import * as React from "react";
import Link from "next/link";
import {
    Home,
    Bed,
    Calendar,
    MessageSquare,
    Settings,
    Users,
    Image as ImageIcon,
    ChevronRight,
    Bell,
    LogOut
} from "lucide-react";

// Sidebar navigation items
const navItems = [
    { icon: Home, label: "Dashboard", href: "/admin", active: true },
    { icon: Bed, label: "Rooms", href: "/admin/rooms" },
    { icon: Calendar, label: "Bookings", href: "/admin/bookings" },
    { icon: MessageSquare, label: "Reviews", href: "/admin/reviews" },
    { icon: ImageIcon, label: "Gallery", href: "/admin/gallery" },
    { icon: Users, label: "Guests", href: "/admin/guests" },
    { icon: Settings, label: "Settings", href: "/admin/settings" },
];

// Mock data
const stats = [
    { label: "Total Bookings", value: "127", change: "+12%", positive: true },
    { label: "Revenue (MTD)", value: "â‚¬8,450", change: "+8%", positive: true },
    { label: "Avg. Rating", value: "9.3", change: "+0.2", positive: true },
    { label: "Occupancy", value: "78%", change: "-3%", positive: false },
];

const recentBookings = [
    { id: "B001", guest: "Sarah Johnson", room: "Family Room with Terrace", checkIn: "Feb 5, 2026", checkOut: "Feb 8, 2026", status: "confirmed" },
    { id: "B002", guest: "Marco Rossi", room: "Deluxe Double Room", checkIn: "Feb 6, 2026", checkOut: "Feb 10, 2026", status: "pending" },
    { id: "B003", guest: "Emma Wilson", room: "Terrace Suite", checkIn: "Feb 8, 2026", checkOut: "Feb 12, 2026", status: "confirmed" },
    { id: "B004", guest: "Hans Mueller", room: "Cozy Double Room", checkIn: "Feb 10, 2026", checkOut: "Feb 14, 2026", status: "confirmed" },
];

export default function AdminDashboard() {
    return (
        <div className="min-h-screen bg-stone-100 flex">
            {/* Sidebar */}
            <aside className="w-64 bg-stone-800 text-white flex-shrink-0 hidden lg:block">
                <div className="p-6">
                    <Link href="/" className="font-serif text-xl">Villa Mosta</Link>
                    <p className="text-stone-400 text-xs mt-1">Admin Dashboard</p>
                </div>

                <nav className="mt-4">
                    {navItems.map((item) => (
                        <Link
                            key={item.href}
                            href={item.href}
                            className={`flex items-center gap-3 px-6 py-3 text-sm transition-colors ${item.active
                                    ? "bg-stone-700 text-white border-l-2 border-amber-500"
                                    : "text-stone-300 hover:bg-stone-700 hover:text-white"
                                }`}
                        >
                            <item.icon className="w-5 h-5" />
                            <span>{item.label}</span>
                        </Link>
                    ))}
                </nav>

                <div className="absolute bottom-0 left-0 w-64 p-4 border-t border-stone-700">
                    <button className="flex items-center gap-2 text-stone-400 hover:text-white text-sm transition-colors">
                        <LogOut className="w-4 h-4" />
                        <span>Sign Out</span>
                    </button>
                </div>
            </aside>

            {/* Main Content */}
            <main className="flex-1 overflow-auto">
                {/* Top Bar */}
                <header className="bg-white border-b border-stone-200 px-6 py-4 flex items-center justify-between">
                    <div>
                        <h1 className="text-xl font-semibold text-stone-800">Dashboard</h1>
                        <p className="text-stone-500 text-sm">Welcome back! Here&apos;s what&apos;s happening.</p>
                    </div>
                    <div className="flex items-center gap-4">
                        <button className="relative p-2 text-stone-500 hover:text-stone-700">
                            <Bell className="w-5 h-5" />
                            <span className="absolute top-1 right-1 w-2 h-2 bg-amber-500 rounded-full"></span>
                        </button>
                        <div className="w-8 h-8 bg-stone-300 rounded-full flex items-center justify-center">
                            <span className="text-stone-600 text-sm font-medium">A</span>
                        </div>
                    </div>
                </header>

                <div className="p-6">
                    {/* Stats Grid */}
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
                        {stats.map((stat) => (
                            <div key={stat.label} className="bg-white p-6 rounded-lg shadow-sm">
                                <p className="text-stone-500 text-sm mb-1">{stat.label}</p>
                                <div className="flex items-end justify-between">
                                    <p className="text-2xl font-semibold text-stone-800">{stat.value}</p>
                                    <span className={`text-sm ${stat.positive ? "text-green-600" : "text-red-500"}`}>
                                        {stat.change}
                                    </span>
                                </div>
                            </div>
                        ))}
                    </div>

                    {/* Recent Bookings */}
                    <div className="bg-white rounded-lg shadow-sm">
                        <div className="p-6 border-b border-stone-100 flex items-center justify-between">
                            <h2 className="font-semibold text-stone-800">Recent Bookings</h2>
                            <Link href="/admin/bookings" className="text-amber-700 text-sm hover:underline flex items-center gap-1">
                                View all <ChevronRight className="w-4 h-4" />
                            </Link>
                        </div>
                        <div className="overflow-x-auto">
                            <table className="w-full">
                                <thead className="bg-stone-50">
                                    <tr>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-stone-500 uppercase tracking-wider">Booking ID</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-stone-500 uppercase tracking-wider">Guest</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-stone-500 uppercase tracking-wider">Room</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-stone-500 uppercase tracking-wider">Check In</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-stone-500 uppercase tracking-wider">Check Out</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-stone-500 uppercase tracking-wider">Status</th>
                                    </tr>
                                </thead>
                                <tbody className="divide-y divide-stone-100">
                                    {recentBookings.map((booking) => (
                                        <tr key={booking.id} className="hover:bg-stone-50">
                                            <td className="px-6 py-4 text-sm font-medium text-stone-800">{booking.id}</td>
                                            <td className="px-6 py-4 text-sm text-stone-600">{booking.guest}</td>
                                            <td className="px-6 py-4 text-sm text-stone-600">{booking.room}</td>
                                            <td className="px-6 py-4 text-sm text-stone-600">{booking.checkIn}</td>
                                            <td className="px-6 py-4 text-sm text-stone-600">{booking.checkOut}</td>
                                            <td className="px-6 py-4">
                                                <span className={`px-2 py-1 text-xs rounded-full ${booking.status === "confirmed"
                                                        ? "bg-green-100 text-green-700"
                                                        : "bg-amber-100 text-amber-700"
                                                    }`}>
                                                    {booking.status}
                                                </span>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>

                    {/* Quick Actions */}
                    <div className="mt-8 grid grid-cols-1 md:grid-cols-3 gap-6">
                        <Link href="/admin/rooms" className="bg-white p-6 rounded-lg shadow-sm hover:shadow-md transition-shadow">
                            <Bed className="w-8 h-8 text-amber-600 mb-3" />
                            <h3 className="font-semibold text-stone-800 mb-1">Manage Rooms</h3>
                            <p className="text-stone-500 text-sm">Edit room details, pricing, and availability</p>
                        </Link>
                        <Link href="/admin/gallery" className="bg-white p-6 rounded-lg shadow-sm hover:shadow-md transition-shadow">
                            <ImageIcon className="w-8 h-8 text-amber-600 mb-3" />
                            <h3 className="font-semibold text-stone-800 mb-1">Update Gallery</h3>
                            <p className="text-stone-500 text-sm">Add or remove photos from the website</p>
                        </Link>
                        <Link href="/admin/settings" className="bg-white p-6 rounded-lg shadow-sm hover:shadow-md transition-shadow">
                            <Settings className="w-8 h-8 text-amber-600 mb-3" />
                            <h3 className="font-semibold text-stone-800 mb-1">Site Settings</h3>
                            <p className="text-stone-500 text-sm">Configure contact info, policies, and more</p>
                        </Link>
                    </div>
                </div>
            </main>
        </div>
    );
}
